"use client";

import { useState } from "react";
import Image from "next/image";
import { Trash2, Minus, Plus } from "lucide-react";


interface CartItem {
  id: number;
  name: string;
  type: string;
  badge: string;
  badgeColor: string;
  price: number;
  negotiatedPrice: number | null;
  quantity: number;
  image: string;
}

const MyCartComp = () => {
  const [cartItems, setCartItems] = useState<CartItem[]>([
    {
      id: 1,
      name: "Ergonomic Office Chair",
      type: "14 offers",
      badge: "See them",
      badgeColor: "text-amber-600",
      price: 310.0,
      negotiatedPrice: 350.0,
      quantity: 1,
      image: SeatImage,
    },
    {
      id: 2,
      name: "Ergonomic Office Chair",
      type: "Home + Office",
      badge: "See item",
      badgeColor: "text-amber-600",
      price: 200.0,
      negotiatedPrice: 350.0,
      quantity: 2,
      image:
        "https://images.unsplash.com/photo-1580480055273-228ff5388ef8?w=100&h=100&fit=crop",
    },
    {
      id: 3,
      name: "Ergonomic Office Chair",
      type: "Home + Office",
      badge: "See item",
      badgeColor: "text-amber-600",
      price: 310.0,
      negotiatedPrice: null,
      quantity: 1,
      image:
        "https://images.unsplash.com/photo-1580480055273-228ff5388ef8?w=100&h=100&fit=crop",
    },
    {
      id: 4,
      name: "Ergonomic Office Chair",
      type: "Home + Office",
      badge: "See item",
      badgeColor: "text-amber-600",
      price: 310.0,
      negotiatedPrice: 350.0,
      quantity: 3,
      image:
        "https://images.unsplash.com/photo-1580480055273-228ff5388ef8?w=100&h=100&fit=crop",
    },
  ]);

  const updateQuantity = (id: number, change: number) => {
    setCartItems((prev) =>
      prev.map((item) =>
        item.id === id
          ? { ...item, quantity: Math.max(1, item.quantity + change) }
          : item
      )
    );
  };

  const removeItem = (id: number) => {
    setCartItems((prev) => prev.filter((item) => item.id !== id));
  };

  const calculateSubtotal = (item: CartItem) =>
    (item.price * item.quantity).toFixed(2);

  const calculateTotal = () =>
    cartItems
      .reduce((sum, item) => sum + item.price * item.quantity, 0)
      .toFixed(2);

  const totalItems = cartItems.reduce((sum, item) => sum + item.quantity, 0);

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-6">
        <h1 className="text-3xl font-bold text-gray-900 mb-8">My Cart</h1>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Cart Items */}
          <div className="lg:col-span-2">
            <div className="bg-white rounded-2xl border border-gray-200 overflow-hidden">
              {/* Header */}
              <div className="grid grid-cols-12 gap-4 px-6 py-4 bg-gray-50 border-b text-sm font-semibold text-gray-700">
                <div className="col-span-5">Product</div>
                <div className="col-span-2">Price</div>
                <div className="col-span-2">Quantity</div>
                <div className="col-span-2">Subtotal</div>
                <div className="col-span-1" />
              </div>

              {/* Items */}
              <div className="divide-y">
                {cartItems.map((item) => (
                  <div
                    key={item.id}
                    className="grid grid-cols-12 gap-4 px-6 py-4 items-center"
                  >
                    {/* Product */}
                    <div className="col-span-5 flex items-center gap-3">
                      <Image
                        src={item.image}
                        alt={item.name}
                        width={64}
                        height={64}
                        className="rounded-lg object-cover"
                      />
                      <div>
                        <h3 className="font-semibold text-gray-900">
                          {item.name}
                        </h3>
                        <div className="text-sm flex gap-2">
                          <span className="text-gray-600">{item.type}</span>
                          <span className={`${item.badgeColor} font-medium`}>
                            {item.badge}
                          </span>
                        </div>
                      </div>
                    </div>

                    {/* Price */}
                    <div className="col-span-2">
                      <div className="font-semibold">
                        ${item.price.toFixed(2)}
                      </div>
                      {item.negotiatedPrice && (
                        <div className="text-sm text-gray-500">
                          Negotiated [{item.negotiatedPrice.toFixed(2)}]
                        </div>
                      )}
                    </div>

                    {/* Quantity */}
                    <div className="col-span-2">
                      <div className="flex items-center border rounded-lg w-fit">
                        <button
                          onClick={() => updateQuantity(item.id, -1)}
                          className="p-2 hover:bg-gray-100"
                        >
                          <Minus className="w-4 h-4" />
                        </button>
                        <span className="px-3 font-medium">
                          {item.quantity}
                        </span>
                        <button
                          onClick={() => updateQuantity(item.id, 1)}
                          className="p-2 hover:bg-gray-100"
                        >
                          <Plus className="w-4 h-4" />
                        </button>
                      </div>
                    </div>

                    {/* Subtotal */}
                    <div className="col-span-2 font-semibold">
                      ${calculateSubtotal(item)}
                    </div>

                    {/* Delete */}
                    <div className="col-span-1 flex justify-end">
                      <button
                        onClick={() => removeItem(item.id)}
                        className="p-2 text-red-500 hover:bg-red-50 rounded-lg"
                      >
                        <Trash2 className="w-5 h-5" />
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Summary */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-2xl border p-6 sticky top-6">
              <h2 className="text-xl font-bold mb-6">Cart Summary</h2>

              <div className="space-y-4 mb-6">
                <div className="flex justify-between">
                  <span>Items total ({totalItems})</span>
                  <span className="font-semibold">${calculateTotal()}</span>
                </div>
                <div className="flex justify-between text-lg font-bold">
                  <span>Subtotal</span>
                  <span>${calculateTotal()}</span>
                </div>
              </div>

              <button className="w-full bg-emerald-900 hover:bg-emerald-800 text-white py-4 rounded-xl font-semibold">
                Proceed To Checkout
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MyCartComp;
